import React, { useEffect, useState } from "react";
import Typewriter from "typewriter-effect";
import useInterval from "./utils/useInterval";
import Num5 from "./assets/Yellow-Number/5.svg";
import NumSpecial from "./assets/Yellow-Number/special.svg";
import Num6 from "./assets/Yellow-Number/6.svg";
import Num9 from "./assets/Yellow-Number/9.svg";
import Num8 from "./assets/Yellow-Number/8.svg";
import Num0 from "./assets/Yellow-Number/0.svg";
import Num2 from "./assets/Yellow-Number/2.svg";
import Num1 from "./assets/Yellow-Number/1.svg";
import T0 from "./assets/bothead-png/T0.png";
import T1 from "./assets/bothead-png/T1.png";
import T2 from "./assets/bothead-png/T2.png";
import T3 from "./assets/bothead-png/T3.png";
import T4 from "./assets/bothead-png/T4.png";
import T5 from "./assets/bothead-png/T5.png";
import twitter from "./assets/twitter.png";
import discord from "./assets/discord.png";
import Button from "./components/Button";
import BannerWebm from "./assets/banner-webm.webm";
import Banner from "./assets/banner.jpeg";
import Title from "./components/Title";
import "./App.less";

const imgList = [T0, T1, T2, T3, T4, T5];

function App() {
  const [imgIndex, setImgIndex] = useState(0);
  const [imgUrl, setImgUrl] = useState<string>(T0);

  useInterval(() => {
    if (imgIndex < 6) {
      setImgUrl(imgList[imgIndex]);
      setImgIndex(imgIndex + 1);
    } else {
      setImgUrl(imgList[0]);
      setImgIndex(0);
    }
  }, 300);

  return (
    <div className="App">
      <div className="Index">
        <Title />
        <div className="social-link">
          <div className="twitter">
            <a
              href="https://twitter.com/BotHeadNFT"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={twitter}
                width="100%"
                className="twitter-logo"
                alt="Twitter"
              />
              Twitter
            </a>
          </div>
          <div className="discord">
            <a
              href="https://twitter.com/BotHeadNFT"
              target="_blank"
              rel="noreferrer"
            >
              <img
                src={discord}
                width="100%"
                className="discord-logo"
                alt="discord"
              />
              Discord
            </a>
          </div>
        </div>
        <div className="open-mint">
          <div id="triangle"></div>
          <div className="open-mint-time-title">COMMING SOON</div>
          {/* <div className="number-count">21:58:34</div> */}
          {/* <div className="number-count">COMMING SOON</div> */}
        </div>
        <div className="bothead-show">
          <img src={imgUrl} className="bothead" alt="bothead" />
        </div>
        <div className="banner">
          <video
            autoPlay
            loop
            muted
            width="100%"
            className="banner-img"
            preload="auto"
            poster={Banner}
          >
            <source src={BannerWebm} type="video/mp4" />
          </video>
          <div className="typer">
            <Typewriter
              onInit={(typewriter) => {
                typewriter
                  .changeDelay(20)
                  .typeString(
                    "BOTHEAD is alive! Shrouded in mystery, the ultra-high-tech BOTHEADs are friendly and curious. They have come to experience the beauty of life on Earth. Their encounters have inspired a deep love and admiration for our world and humanity, which each individual BOTHEAD exhibits in its own unique way."
                  )
                  .pauseFor(300)
                  .typeString(
                    ` <br /><br /> Exploring the vast planet, some BOTHEADs have traveled to the wilderness to behold nature’s miracles, while others are in the cities marveling at the sites. Others still may be found in your kitchen or backyard, captivated by the simple pleasures available all around us. They are planet Earth’s number one fan, and their presence is surely a sign of greater things yet to come!`
                  )
                  .pauseFor(300)
                  .typeString(
                    ` <br /><br /> Their mission here is far from over. In fact, we are just beginning to understand how they intend to share their advanced technologies and secrets of the universe to promote humanity’s achievements and boost the beauty of our world to unprecedented levels!`
                  )
                  .callFunction(() => {
                    console.log("String typed out!");
                  })
                  // .deleteAll()
                  .callFunction(() => {
                    console.log("All strings were deleted");
                  })
                  .start();
              }}
            />
          </div>
        </div>
        <div className="footer">© 2021 BotHead Collection</div>
        {/* <div className="mint-button">
          <Button></Button>
        </div> */}
      </div>
    </div>
  );
}

export default App;
